const allStrings = [[{ "nodeName": "div", "children": [{ "nodeName": "small", "children": [{ "nodeName": "text", "textContent": "(" }, { "nodeName": "text", "textContent": ":tagvar0:" }, { "nodeName": "text", "textContent": " render count " }, { "nodeName": "span", "attributes": [["id", ":tagvar1:"]], "children": [{ "nodeName": "text", "textContent": ":tagvar2:" }] }, { "nodeName": "text", "textContent": ")" }] }] }]];
